import Price from "../components/Price.vue";

export default {
  name: "checkout",
  props: ["cart", "cartTotal"],
  components: {
    Price
  },
  data: function() {
    return {
      submitted: false
    }
  },
  methods: {
    onSubmitClick: function () {
      if (!this.$store.state.loginStatus) {
        if (confirm("Only registered user can place the order. Click ok to login and complete the shopping.")) {
          this.$router.push("login");
        }
      } else {
        if (confirm("You have succeffully completed the payment.")) {
          this.submitted = true;
          this.$root.$emit('updateCart', this.submitted);
          this.$router.push("home");
        }
      }
    }
  }
};