<template>
  <div class="container">
    <div class="card">
      <form @submit.prevent="onSubmitregister">
        <p class="h4 text-center mb-4">Sign up</p>
        <label for="defaultFormRegisterNameEx" class="grey-text"
          >Your name</label
        >
        <input
          type="text"
          v-model="yourName"
          class="form-control"
          v-on:blur="validateName()"
          required
        />
        <span v-if="!namematch">Please enter first name and last name</span>
        <span v-if="!yourName">This field required</span>
        <br />
        <label for="defaultFormRegisterEmailEx" class="grey-text"
          >Your email</label
        >
        <input
          type="email"
          v-model.trim="yourEmail"
          class="form-control"
          required
        />
        <span v-if="!yourEmail">This field required</span>
        <br />
        <label for="defaultFormRegisterConfirmEx" class="grey-text"
          >Confirm your email</label
        >
        <input
          type="email"
          v-model.trim="confirmYourEmail"
          class="form-control"
          v-on:blur="validateEmail()"
          required
        />
        <span v-if="!emailMatch">Email and Confrim email is not matching</span>
        <span v-if="!confirmYourEmail">This field required</span>
        <br />
        <label for="defaultFormRegisterPasswordEx" class="grey-text"
          >Your password</label
        >
        <input
          type="password"
          v-model="yourPassword"
          class="form-control"
          v-on:blur="validatePassword()"
          required
        />
        <span v-if="!passwordmatch">Password must be atleast 6 charecters</span>
        <span v-if="!yourPassword">This field required</span>
        <div class="text-center mt-4">
          <button class="btn btn-primary" type="submit">Register</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      yourName: null,
      yourEmail: null,
      confirmYourEmail: null,
      yourPassword: null,
      namematch: true,
      emailMatch: true,
      passwordmatch: true,
      showModal: false,
    };
  },
  methods: {
    validateName: function () {
      var pattern = /^(?:[\u00c0-\u01ffa-zA-Z'-]){2,}(?:\s[\u00c0-\u01ffa-zA-Z'-]{2,})+$/i;
      if (!pattern.test(this.yourName)) {
        this.namematch = false;
      } else {
        this.namematch = true;
      }
    },

    validateEmail: function () {
      if (this.yourEmail != this.confirmYourEmail) {
        this.emailMatch = false;
      } else {
        this.emailMatch = true;
      }
    },

    validatePassword: function () {
      var pattern = /.{6,}/;
      if (!pattern.test(this.yourPassword)) {
        this.passwordmatch = false;
      } else {
        this.passwordmatch = true;
      }
    },

    onSubmitregister: function () {
      if (confirm("You are registered successfully! Please login from home.")) {
        this.$router.push("home");
      }
    },
  },
};
</script>

<style scoped>
input:valid {
  background-color: #ddffdd;
}
input:focus:required:invalid {
  border-color: #800000;
  border-width: 3px;
}
span {
  color: red;
  float: right;
}

.card {
  background-color: rgba(255, 255, 255, 1);
  height: 560px;
  width: 100%;
  transition: 0.3s ease-in-out;
  z-index: 0;
  box-shadow: 0 0 15px 9px #00000096;
}

form {
  padding: 50px;
}
</style>