import Price from "../components/Price.vue";

export default {
  name: "navbar",
  data: function() {
    return {
      showFilter: true
    }
  },
  props: ["cart", "cartQty", "cartTotal"],
  components: {
    Price
  }, 
  mounted: function() {
    this.$root.$on('updateCart', data => {
        if(data) {
          this.$root.$emit("cart", []) ;
        }
    });
  },
  methods: {
    showhideFiler: function() {
      this.showFilter = !this.showFilter;
    }
  },
  watch: {
    cartQty: function(newValue, oldvalue) {
      console.log(newValue);
      console.log(oldvalue);
    }
  }
};