<template>
  <div class="container">
    <div class="card">
      <form @submit.prevent="onSubmitLogin">
        <p class="h4 text-center mb-4">Sign in</p>
        <label for="defaultFormLoginEmailEx" class="grey-text"
          >Your email</label
        >
        <input
          type="email"
          v-model.trim="yourEmail"
          class="form-control"
          required
        />
        <span v-if="!yourEmail">This field required</span>
        <br />
        <label for="defaultFormLoginPasswordEx" class="grey-text"
          >Your password</label
        >
        <input
          type="password"
          v-model.trim="yourPassword"
          class="form-control"
          required
        />
        <span v-if="!yourPassword">This field required</span>
        <div class="text-center mt-4">
          <button class="btn btn-primary" type="submit">Login</button>
        </div>

        <div class="card-footer">
				<div class="d-flex justify-content-center links">
					Don't have an account?<button type="button" class="btn btn-link" @click="$router.push('register')">Sign Up</button>
				</div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      yourEmail: null,
      yourPassword: null,
    };
  },
  methods: {
    onSubmitLogin: function () {
      this.$store.state.loginStatus = true;
      if (confirm("You are logged in successfully!.")) {
        this.$router.push("home");
      }
    },
  },
};
</script>

<style scoped>
input:valid {
  background-color: #ddffdd;
}

input:focus:required:invalid {
  border-color: #800000;
  border-width: 3px;
}

span {
  color: red;
  float: right;
}

.card {
  background-color: rgba(255, 255, 255, 1);
  height: 400px;
  width: 100%;
  transition: 0.3s ease-in-out;
  z-index: 0;
  box-shadow: 0 0 15px 9px #00000096;
}

form {
  padding: 50px;
}
.btn-link {
  font-weight: 700;
    margin-top: -6px;
    color: darkblue;
    margin-left: -10px;
}
</style>